import commonList from '@/components/common/common-list.vue';
import topicList from '@/components/community/topic-list.vue';
import userList from '@/components/user-list/user-list.vue';
export default {
	components:{
		commonList,
		topicList,
		userList
	},
	data() {
		return {
			searchText:"",
			list:['斗破苍穹','大主宰','吞噬星空','斗罗大陆'],
			// 搜索结果
			searchList:[],
			// 当前搜索类型
			type:""
		}
	},
	// 监听导航输入
	onNavigationBarSearchInputChanged(e) {
		this.searchText = e.text;
	},
	// 监听点击导航搜索按钮
	onNavigationBarButtonTap(e) {
		if (e.index === 0) {
			this.searchEvent()
		}
	},
	onLoad(e) {
		if (e.type) {
			this.type = e.type
		}
		let pageTitle = '小说'
		switch (this.type){
			case 'book':
			pageTitle = '小说'
				break;
			case 'topic':
			pageTitle = '话题'
				break;
			case 'user':
			pageTitle = '用户'
				break;
		}
		// 修改搜索占位








	},
	methods: {
		// 点击搜索历史
		clickSearchHistory(text){
			this.searchText = text
			this.searchEvent()
		},
		// 搜索事件
		searchEvent(){
			// 收起键盘
			uni.hideKeyboard()
			// 处于loading状态
			uni.showLoading({
				title: '加载中',
				mask: false
			})
			// 请求搜索
			setTimeout(()=>{
				switch (this.type){
					case 'book':
					this.searchList = demo1
						break;
					case 'topic':
					this.searchList = demo2
						break;
					case 'user':
					this.searchList = demo3
						break;
				}
				// 隐藏loading
				uni.hideLoading()
			},3000)
		}
	}
}
